export interface ApiGetPaginatedRequestDTO<T = unknown, F = unknown> {
  page: number
  perPage: number
  sortBy: T
  orderBy: OrderBy
  filters?: F
}

export interface ApiGetRequestDTO<T = unknown, F = unknown> {
  sortBy?: T
  orderBy?: OrderBy
  filters?: F
}

export interface IPaginationOptions {
  currentPage: number
  lastPage?: number
  perPage: number
  totalItems?: number
}

export interface IUpdatePaginationPayload {
  currentPage: number
  perPage: number
}

export enum OrderBy {
  ASC = 'asc',
  DESC = 'desc',
}
